  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.ticket-list {
  margin: 40px auto;
  border: 1px solid #ccc;
  padding: 10px;
  width: 800px;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
}

.ticket {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin-bottom: 5px;
  width: 100%;
  border: 1px solid #ddd;
  padding: 8px 10px;
  border-radius: 3px;
  background-color: #f9f9f9;
  position: relative;
  cursor: pointer;
}

.ticket:hover {
  background-color: #ededed;
}

.short {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.detailDescription {
  display: none;
  font-size: 14px;
  color: #444;
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid #ddd;
  line-height: 1.5;
  margin-left: 40px;
}

.ticket input[type="checkbox"] {
  opacity: 0;
  position: absolute;
  width: 20px;
  height: 20px;
  cursor: pointer;
  z-index: 1;
  left: 10px;
}

.ticket label::before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid #ccc;
  background-color: #fff;
  margin-right: 15px;
  border-radius: 50%;
  position: absolute;
  left: 10px;
  top: 25px;
  transform: translateY(-50%);
}

.ticket input[type="checkbox"]:checked+label::before {
  background-color: hsl(0, 100%, 77%);
  border-color: hsl(0, 100%, 77%);
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px;
}

.ticket label {
  flex-grow: 1;
  font-size: 14px;
  padding-left: 40px;
}

.date {
  margin-left: auto;
  margin-right: 10px;
  font-size: 12px;
  color: #777;
}

.edit,
.delete {
  border: 1px solid #ccc;
  background-color: #fff;
  padding: 4px 8px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 12px;
  line-height: 1;
  margin-left: 10px;
}

img:hover {
  background-color: hsl(0, 100%, 77%);
}

.add-ticket {
  float: right;
  margin-bottom: 10px;
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-radius: 3px;
  background-color: #f0f0f0;
  cursor: pointer;
  font-size: 14px;
}

.add-ticket:hover {
  background-color: hsl(0, 100%, 77%);
}

.ticket-list h2 {
  margin-top: 0;
  margin-bottom: 10px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: none;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modal-content {
  background-color: #fff;
  padding: 20px 30px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 90%;
  margin: 100px auto;
}

.modal-content h2,
.modal-content h3 {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.modal-content p {
  font-size: 1rem;
  color: #444;
  margin-bottom: 20px;
}

.modal-content input,
.modal-content textarea {
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.modal-content textarea {
  resize: none;
  height: 100px;
}

.modal-actions,
.modal-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.modal-button,
.modal-buttons button {
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.modal-button.cancel,
.modal-buttons button.cancel {
  background-color: #f1f1f1;
  color: #333;
}

.modal-button.cancel:hover,
.modal-buttons button.cancel:hover {
  background-color: #e0e0e0;
}

.modal-button.confirm,
.modal-buttons button.confirm {
  background-color: #ff4d4d;
  color: #fff;
}

.modal-button.confirm:hover,
.modal-buttons button.confirm:hover {
  background-color: #e63939;
}

.modal-buttons button:hover {
  background: hsl(0, 100%, 77%);
}
